@font-face {
  font-family: "Rajdhani";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/rajdhani-v14-latin-regular.woff2") format("woff2"), url("../fonts/rajdhani-v14-latin-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Rajdhani";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/rajdhani-v14-latin-500.woff2") format("woff22"), url("../fonts/rajdhani-v14-latin-500.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Rajdhani";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/rajdhani-v14-latin-600.woff2") format("woff22"), url("../fonts/rajdhani-v14-latin-600.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Rajdhani";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/rajdhani-v14-latin-700.woff2") format("woff22"), url("../fonts/rajdhani-v14-latin-700.woff") fotmat("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.page {
  height: 100%;
  font-family: "Rajdhani", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  line-height: 24px;
}

.page__body {
  display: flex;
  flex-direction: column;
  margin: 0;
  min-height: 100%;
  background-color: #ffffff;
}
@media (min-width: 1360px) {
  .page__body {
    max-width: 1360px;
    margin: 0 auto;
  }
}

.page-product {
  flex-grow: 1;
  margin-bottom: 2px;
}
@media (min-width: 720px) {
  .page-product {
    margin-bottom: 0;
  }
}

.new-collection {
  margin-bottom: 40px;
}
@media (min-width: 1360px) {
  .new-collection {
    display: grid;
    grid-template-columns: 600px 1fr;
    gap: 92px;
    margin-bottom: 64px;
  }
}

@media (min-width: 1360px) {
  .new-collection__carousel {
    grid-column: 1/2;
  }
}

.new-collection__wrapper {
  margin: 0 16px;
}
@media (min-width: 1360px) {
  .new-collection__wrapper {
    grid-column: 2/-1;
    margin: 0 52px 0 0;
  }
}

.new-collection__button {
  display: block;
  border: 1px solid transparent;
  background-color: #192b3f;
  margin: 0;
  color: #ffffff;
  cursor: pointer;
  font-size: 24px;
  line-height: 32px;
  font-family: inherit;
  font-weight: 500;
  width: 100%;
  padding: 12px;
  box-sizing: border-box;
  outline: none;
  padding: 11px;
  margin-bottom: 23px;
  outline: none;
  border: 1px solid transparent;
  box-sizing: border-box;
}
@media (min-width: 1360px) {
  .new-collection__button {
    margin-bottom: 16px;
  }
}
.new-collection__button:hover {
  background-color: #2e4055;
}
.new-collection__button:focus {
  border-color: #b8814e;
  border-width: 2px;
}
.new-collection__button:active {
  background-color: #3b536d;
}

.carousel {
  position: relative;
}
.carousel::after {
  content: "";
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.22) 0, rgba(53, 41, 37, 0.5) 100%);
  mix-blend-mode: multiply;
  width: 100%;
  height: 193px;
  position: absolute;
  bottom: 0;
  z-index: 0;
}
@media (min-width: 1360px) {
  .carousel {
    position: fixed;
  }
}

.carousel__header-logo {
  text-decoration: none;
  cursor: pointer;
  outline: none;
}
.carousel__header-logo:focus .carousel__header-image {
  border-color: #b8814e;
  border-width: 2px;
}

.carousel__header-image {
  position: absolute;
  top: 24px;
  left: 16px;
  width: 65px;
  height: 36px;
  border: 1px solid transparent;
}
@media (min-width: 1360px) {
  .carousel__header-image {
    top: 32px;
    left: 52px;
    width: 95px;
    height: 53px;
  }
}

.carousel__controls {
  position: absolute;
  z-index: 1;
  bottom: 4.5%;
  left: 16px;
}
@media (min-width: 1360px) {
  .carousel__controls {
    left: 52px;
    top: 704px;
    bottom: 0;
  }
}

.controls {
  display: flex;
  justify-content: space-between;
  gap: 16px;
}

.controls__button {
  display: block;
  border: 1px solid transparent;
  background-color: #192b3f;
  margin: 0;
  color: #ffffff;
  cursor: pointer;
  font-size: 24px;
  line-height: 32px;
  font-family: inherit;
  font-weight: 500;
  width: 100%;
  padding: 12px;
  box-sizing: border-box;
  outline: none;
  width: 32px;
  height: 32px;
  border: 1px solid transparent;
  border-radius: 50%;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  padding: 0;
  position: relative;
}
.controls__button:hover {
  border-color: #ffffff;
}
.controls__button:focus {
  border-color: #b8814e;
  border-width: 2px;
  background-color: transparent;
}
.controls__button:active {
  background-color: #ffffff;
  border-color: #ffffff;
}

.controls__button:active .controls__button-icon {
  fill: #8a94a6;
}

.controls__button-next {
  transform: scale(-1);
}

.product-info__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.product-info__list-item:not(:nth-of-type(1)) {
  background-color: #192b3f;
  display: none;
}

.product-info__image {
  object-fit: cover;
  width: 100%;
  height: 600px;
}
@media (min-width: 932px) {
  .product-info__image {
    height: 768px;
    width: 100%;
  }
  .product-info__image--light-coat {
    object-position: 0 0;
  }
}
@media (min-width: 1360px) {
  .product-info__image {
    width: 600px;
  }
}

.product-info__list-item-wrapper {
  position: absolute;
  bottom: 4.5%;
  padding: 0 16px;
  box-sizing: border-box;
}
@media (min-width: 1360px) {
  .product-info__list-item-wrapper {
    left: 36px;
    top: 567px;
    bottom: 0;
  }
}

.product-info__list-title {
  font-size: 48px;
  line-height: 48px;
  font-weight: 400;
  color: #ffffff;
  margin: 0;
  z-index: 1;
  position: relative;
}
@media (min-width: 720px) {
  .product-info__list-title {
    font-size: 64px;
    line-height: 64px;
  }
}
@media (min-width: 1360px) {
  .product-info__list-title {
    font-size: 72px;
    line-height: 72px;
  }
}

.product-info__list-description {
  font-weight: 500;
  opacity: 0.8;
  color: #ffffff;
  margin: 0;
  margin-bottom: 32px;
  z-index: 1;
  position: relative;
}
@media (min-width: 720px) {
  .product-info__list-description {
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 24px;
  }
}
@media (min-width: 1360px) {
  .product-info__list-description {
    font-size: 28px;
    margin-bottom: 33px;
  }
}

.product-info__list-number {
  font-size: 20px;
  line-height: 32px;
  font-weight: 500;
  color: #ffffff;
  margin: 0;
  margin-left: 160px;
  display: flex;
  align-items: center;
  z-index: 1;
  position: relative;
}

.product-info__list-line {
  content: "";
  width: 16px;
  height: 1px;
  background-color: #ffffff;
  display: block;
}

.product-info__list-number-last {
  opacity: 0.5;
}

.parameters__container {
  margin-bottom: 31px;
  position: relative;
  padding: 16px 0;
}
.parameters__container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #e1e6ed;
}
.parameters__container::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #e1e6ed;
}
@media (min-width: 720px) {
  .parameters__container {
    padding-top: 24px;
    padding-bottom: 14px;
    margin-bottom: 21px;
  }
}
@media (min-width: 1360px) {
  .parameters__container {
    padding-top: 16px;
    margin-bottom: 31px;
  }
}

@media (min-width: 720px) {
  .parameters__container-color-size {
    display: grid;
    grid-template-columns: 148px 1fr;
    gap: 72px;
  }
}
@media (min-width: 1360px) {
  .parameters__container-color-size {
    gap: 13px;
  }
}

.parameters__size {
  margin-bottom: 40px;
}
@media (min-width: 1360px) {
  .parameters__size {
    margin-bottom: 37px;
  }
}

.parameters__fashion {
  margin-bottom: 16px;
}
@media (min-width: 720px) {
  .parameters__fashion {
    margin-bottom: 4px;
  }
}
@media (min-width: 1360px) {
  .parameters__fashion {
    margin-top: 57px;
    margin-bottom: 8px;
  }
}

@media (min-width: 720px) {
  .parameters__size-wrapper {
    display: grid;
    grid-template-columns: 240px 1fr;
    gap: 19px;
    align-items: center;
  }
}
@media (min-width: 1360px) {
  .parameters__size-wrapper {
    grid-template-columns: 177px 1fr;
  }
}

.parameters__colour {
  margin-bottom: 29px;
}

.parameters__size-container {
  position: relative;
  top: 9px;
}

.parameters__size-link {
  text-decoration: none;
  cursor: pointer;
  outline: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #c9b8a8;
  outline: none;
  width: 92px;
  height: 28px;
  border: 1px solid #c9b8a8;
  box-sizing: border-box;
  border-radius: 24px;
  padding: 2.5px 11px;
}
.parameters__size-link:focus {
  border-color: #b8814e;
  border-width: 2px;
}

.parameters__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}
@media (min-width: 720px) {
  .parameters__wrapper {
    grid-template-columns: max-content 1fr;
    gap: 72px;
  }
}

@media (min-width: 720px) {
  .parameters__cost {
    margin-left: 70px;
  }
}
@media (min-width: 1360px) {
  .parameters__cost {
    margin-left: 11px;
  }
}

.fashion__title {
  font-weight: 500;
  font-size: 32px;
  line-height: 100%;
  color: #192b3f;
  margin: 0;
  margin-bottom: 14px;
}
@media (min-width: 720px) {
  .fashion__title {
    font-size: 48px;
    line-height: 56px;
  }
}

.fashion__description {
  font-weight: 500;
  font-size: 18px;
  line-height: 28px;
  color: #8a94a6;
  opacity: 0.8;
  margin: 0;
  width: 99%;
}
@media (min-width: 720px) {
  .fashion__description {
    width: 91%;
    font-size: 20px;
    line-height: 32px;
  }
}
@media (min-width: 1360px) {
  .fashion__description {
    max-width: 383px;
  }
}

.colour-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-left: 4px;
}

.colour-list__control,
.size-list__control {
  cursor: pointer;
}

.colour-list__control-mark {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #192b3f;
  box-sizing: border-box;
  position: relative;
  outline: none;
  border: none;
  padding: 0;
  margin: 0;
  display: block;
}
.colour-list__control-mark::before {
  content: "";
  width: 48px;
  height: 48px;
  border: 1px solid transparent;
  border-radius: 50%;
  background-color: #ffffff;
  position: absolute;
  z-index: -1;
  top: 0;
  left: -4px;
  right: 0;
  bottom: 0;
  margin: auto;
  box-sizing: border-box;
}
.colour-list__control-mark--gray {
  background-color: #8a94a6;
}
.colour-list__control-mark--blue {
  background-color: #3d5876;
}
@media (min-width: 1360px) {
  .colour-list__control-mark {
    width: 25px;
    height: 25px;
  }
  .colour-list__control-mark::before {
    content: "";
    width: 31px;
    height: 31px;
    left: -3px;
  }
}

.colour-list__control-radio[type=radio]:checked + .colour-list__control-mark::before {
  border-color: #192b3f;
}

.colour-list__control-radio[type=radio]:hover + .colour-list__control-mark::before {
  border-color: #8a94a6;
}

.colour-list__control-radio[type=radio]:focus + .colour-list__control-mark::before {
  border-color: #b8814e;
  border-width: 2px;
}

.colour-list__control-radio[type=radio]:active + .colour-list__control-mark::before {
  border-color: #192b3f;
}

.size-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  position: relative;
  left: -12px;
  gap: 6px;
  flex-wrap: wrap;
  margin-bottom: 16px;
}
@media (min-width: 720px) {
  .size-list {
    margin-bottom: 0;
    gap: 0;
  }
}
@media (min-width: 1360px) {
  .size-list {
    top: 8px;
    left: 2px;
  }
}

.size-list__item {
  position: relative;
}

.size-list__control-radio[type=radio]:disabled ~ .size-list__control-label {
  color: #e1e6ed;
  opacity: 0.5;
}

.size-list__control-mark {
  background-color: transparent;
  width: 48px;
  height: 48px;
  border: 1px solid transparent;
  border-radius: 50%;
  box-sizing: border-box;
  display: block;
}
@media (min-width: 1360px) {
  .size-list__control-mark {
    width: 31px;
    height: 31px;
  }
}

.size-list__control-label {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: inherit;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  color: #8a94a6;
}
@media (min-width: 1360px) {
  .size-list__control-label {
    font-size: 16px;
  }
}

.size-list__control-radio[type=radio]:checked + .size-list__control-mark {
  border-color: #192b3f;
}

.size-list__control-radio[type=radio]:checked ~ .size-list__control-label {
  font-size: 28px;
  color: #192b3f;
}
@media (min-width: 1360px) {
  .size-list__control-radio[type=radio]:checked ~ .size-list__control-label {
    font-size: 20px;
  }
}

.size-list__control-radio[type=radio]:not(:disabled):hover + .size-list__control-mark {
  border-color: #8a94a6;
}

.size-list__control-radio[type=radio]:not(:disabled):hover ~ .size-list__control-label {
  color: #8a94a6;
  font-size: 16px;
}

.size-list__control-radio[type=radio]:not(:disabled):focus + .size-list__control-mark {
  border-color: #b8814e;
  border-width: 2px;
}

.size-list__control-radio[type=radio]:not(:disabled):focus ~ .size-list__control-label {
  color: #b8814e;
  font-size: 28px;
}
@media (min-width: 1360px) {
  .size-list__control-radio[type=radio]:not(:disabled):focus ~ .size-list__control-label {
    font-size: 20px;
  }
}

.size-list__control-radio[type=radio]:not(:disabled):active + .size-list__control-mark {
  border-color: #192b3f;
}

.size-list__control-radio[type=radio]:not(:disabled):active ~ .size-list__control-label {
  color: #192b3f;
  font-size: 28px;
}
@media (min-width: 1360px) {
  .size-list__control-radio[type=radio]:not(:disabled):active ~ .size-list__control-label {
    font-size: 20px;
  }
}

.parameters-title {
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  text-transform: uppercase;
  color: #8a94a6;
  margin: 8px 0;
}
.parameters-title--color {
  margin-top: 3px;
  margin-bottom: 11px;
}
.parameters-title--size {
  margin-bottom: 0;
}
@media (min-width: 720px) {
  .parameters-title--size {
    margin-top: 3px;
  }
}
.parameters-title--fashion {
  margin-bottom: 16px;
}
@media (min-width: 1360px) {
  .parameters-title--fashion {
    margin-bottom: 23px;
  }
}
.parameters-title--quantity {
  margin-top: 4px;
  margin-bottom: 8px;
}
.parameters-title--cost {
  margin-top: 4px;
  margin-bottom: 5px;
}
@media (min-width: 1360px) {
  .parameters-title--details {
    margin-bottom: 6px;
  }
}

.quantity {
  display: flex;
  align-items: center;
}

.quantity__button {
  border: 1px solid transparent;
  outline: none;
  background-color: transparent;
  display: flex;
  width: 16px;
  height: 16px;
  box-sizing: border-box;
  cursor: pointer;
}
.quantity__button--remove {
  background: url("../img/quantity-button-remove.svg") no-repeat center;
}
.quantity__button--add {
  background: url("../img/quantity-button-add.svg") no-repeat center;
}
.quantity__button:focus {
  border-color: #b8814e;
  border-width: 2px;
}

.quantity__text {
  cursor: pointer;
  outline: none;
  border: 2px solid transparent;
  box-sizing: border-box;
  font-weight: 500;
  font-size: 24px;
  text-transform: uppercase;
  color: #8a94a6;
  font-family: inherit;
  max-width: 46px;
  text-align: center;
  appearance: textfield;
}
.quantity__text::-webkit-outer-spin-button, .quantity__text::-webkit-inner-spin-button {
  appearance: none;
}

.cost__text {
  font-weight: 500;
  font-size: 40px;
  line-height: 40px;
  color: #c9b8a8;
  margin: 0;
}

.cost__text-hundredth {
  font-size: 28px;
  color: #8a94a6;
}

.details__text {
  font-weight: 500;
  color: #192b3f;
  opacity: 0.8;
  margin: 0;
  margin-bottom: 8px;
}
@media (min-width: 720px) {
  .details__text {
    min-width: 96%;
  }
}
@media (min-width: 1360px) {
  .details__text {
    max-width: 99%;
  }
}

.details__consist {
  font-weight: 500;
  line-height: 20px;
  color: #8a94a6;
  margin: 0;
  margin-bottom: 40px;
}
@media (min-width: 1360px) {
  .details__consist {
    margin-bottom: 72px;
  }
}

.services {
  margin-bottom: 40px;
}

.services__block {
  padding-bottom: 8px;
  position: relative;
  padding-top: 15px;
}
.services__block::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #e1e6ed;
}

.services__link {
  text-decoration: none;
  cursor: pointer;
  outline: none;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  color: #192b3f;
  display: grid;
  grid-template-columns: 1fr 12px;
  align-items: center;
  outline: none;
  border: 1px solid transparent;
  box-sizing: border-box;
}
.services__link::after {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  background: url("../img/quantity-button-add.svg") no-repeat center;
}
@media (min-width: 1360px) {
  .services__link {
    grid-template-columns: 1fr 10px;
  }
}

.services__link--opened::after {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  background: url("../img/quantity-button-remove.svg") no-repeat center;
}

.services__container {
  display: none;
}
.services__container--opened {
  display: block;
}

.services__text {
  margin: 0;
  color: #8a94a6;
  text-align: justify;
}
.services__text--note {
  margin-bottom: 16px;
}

.services__link:focus {
  border-color: #b8814e;
  border-width: 2px;
}

.collections-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 1360px) {
  .collections-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 24px;
  }
}

.collections-list__item {
  background: #c4c4c4 linear-gradient(180deg, rgba(255, 255, 255, 0.22) 0%, rgba(53, 41, 37, 0.22) 100%);
  border-radius: 4px;
  margin-bottom: 16px;
  background-size: cover;
  min-height: 160px;
}
@media (min-width: 432px) {
  .collections-list__item {
    min-height: 287px;
    margin-bottom: 28px;
  }
}
@media (min-width: 1360px) {
  .collections-list__item {
    margin-bottom: 0;
    min-height: 160px;
  }
}
.collections-list__item--kingsman-style {
  background-image: url("../img/kingsman-style-mobile.jpg");
  background-repeat: no-repeat;
}
@media (min-resolution: 2dppx) {
  .collections-list__item--kingsman-style {
    background-image: url("../img/kingsman-style-mobile@2x.jpg");
