  border-color: #b8814e;
  border-width: 2px;
}

.fieldset__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
}

.fieldset__title {
  font-weight: 500;
  font-size: 20px;
  line-height: 32px;
  color: #8a94a6;
}
