  margin: 0;
  margin-bottom: 4px;
}
@media (min-width: 720px) {
  .fieldset__title--gender {
    margin-bottom: 0;
  }
}

.fieldset__control-label {
  font-weight: 500;
  color: #192b3f;
  opacity: 0.8;
}

.fieldset__control-mark {
  display: block;
  box-sizing: border-box;
  position: relative;
  top: 2px;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  outline: none;
}
.fieldset__control-mark--radio {
  border-radius: 50%;
  border: 1px solid #e1e6ed;
}
.fieldset__control-mark--radio::before {
  content: "";
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: transparent;
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.fieldset__control-mark--checkbox {
  width: 20px;
  height: 20px;
  border-radius: 1px;
  border: 1px solid #e1e6ed;
}
@media (min-width: 1360px) {
  .fieldset__control-mark {
    top: 0;
  }
}

.fieldset__control-mark-icon {
  position: absolute;
  top: 7px;
  left: 5px;
}
@media (min-width: 1360px) {
  .fieldset__control-mark-icon {
    top: 5px;
  }
}

.fieldset__control-radio[type=radio]:checked + .fieldset__control-mark--radio::before {
  background-color: #192b3f;
}

.fieldset__control-radio[type=radio]:checked:focus + .fieldset__control-mark {
  border-color: #b8814e;
  border-width: 2px;
}

.fieldset__control-radio[type=radio]:checked:focus ~ .fieldset__control-mark::before {
  background-color: #b8814e;
}

.fieldset__control-radio[type=radio]:checked:focus ~ .fieldset__control-label {
  color: #b8814e;
}

.fieldset__control-checkbox[type=checkbox]:checked + .fieldset__control-mark--checkbox {
  background-color: #192b3f;
  border-color: transparent;
}

.fieldset__control-checkbox[type=checkbox]:checked ~ .fieldset__control-mark-icon {
  fill: #ffffff;
}